<template>
	<v-container fill-height align-center justify-center>
		<v-row>
			<v-col
				class="col-wrapper"
				align-self="center"
				cols="12"
				md="8"
			>
				<video-container v-if="isStarted" @goBack="stopStream"/>
				<welcome-panel v-else @startStream="startStream"/>
			</v-col>
			<v-col
				class="col-wrapper tight"
				align-self="center"
				cols="12"
				md="4"
			>
				<v-container fill-height align-center justify-center>
					<v-row>
						<v-col cols="12">
							<count-down @onExpired="switchMusic"/>
						</v-col>
						<v-col cols="12">
							<image-gallery v-if="isStarted && isTimerExpired"/>
						</v-col>
					</v-row>
				</v-container>
				<v-row>
				</v-row>
			</v-col>
		</v-row>
		<audio src="../assets/music/christmas_calm_music.mp3" id="music-before" loop></audio>
		<audio src="../assets/music/christmas_happy_music.mp3" id="music-after" loop></audio>
	</v-container>
</template>

<script>
	import VideoContainer from "../components/VideoContainer";
	import CountDown from "../components/CountDown";
	import WelcomePanel from "../components/WelcomePanel";
	import ImageGallery from "../components/ImageGallery";
	export default {
		name: "MainPage",
		components: {ImageGallery, WelcomePanel, CountDown, VideoContainer},
		data() {
			return {
				isStarted: false,
				isTimerExpired: false,
				musicEnabled: true,
			}
		},
		methods: {
			startStream() {
				const vm = this;

				vm.isStarted = true;

				if (vm.musicEnabled) document.getElementById(vm.isTimerExpired ? 'music-after' : 'music-before').play();
			},
			stopStream() {
				const vm = this;

				vm.isStarted = false;

				if (vm.musicEnabled) document.getElementById(vm.isTimerExpired ? 'music-after' : 'music-before').pause();
			},
			switchMusic() {
				const vm = this;

				vm.isTimerExpired = true;

				if (vm.isStarted) {
					if (vm.musicEnabled) document.getElementById('music-before').pause();
					if (vm.musicEnabled) document.getElementById('music-after').play();
				}
			}
		}
	}
</script>

<style scoped lang="scss">
	p {
		text-align: center;
	}

	.content-container {
		height: 100%;
	}

	.col-wrapper {
		align-items: center;
		text-align: center;
		display: flex;
		justify-content: center;

		&.tight {

		}
	}
</style>
